<template>
  <el-form class="login-container" label-position="left" label-width="0px">
    <h3 class="login_title">登录系统</h3>
    <el-form-item>
      <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item>
      <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码" suffix-icon="iconfont el_icon_aliicon-test1"></el-input>
    </el-form-item>
    <el-form-item style="width: 100%;">
      <el-button type="primary" style="width: 100%; background: #425bff; border: none;" @click="login()">登录</el-button>
    </el-form-item>
    <div style="margin-top: -10px;">
      <el-link  href="#" target="_blank" style="margin-left:0%;margin-right: 60%; color: cornflowerblue; " :underline="false" >忘记密码</el-link>
      <el-link  href="#" target="_blank" :underline="false" style="color: cornflowerblue;" >注册</el-link>
    </div>
  </el-form>

</template>
<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      responseResult: []
    }
  },
  methods: {
    login () {
      this.$axios
        .post('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        .then(successResponse => {
          if (successResponse.data.code === 200) {
            this.$router.replace({path: '/index'})
          }
        })
        .catch(failResponse => {})
    }
  }

}
</script>

<style scoped>
  .login-container{
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #caca6c6;
  }

  .login_title{
    margin: 0 auto 40px auto;
    text-align: center;
    color: #505458;
  }

</style>



